// You can delete this file if you're not using it
import React from 'react'
import 'whatwg-fetch'
import wrapWithProvider from './with-provider';
import isIE from './src/components/utils/isIE'

export const wrapRootElement = isIE
  ? () => (
    <div style={{ padding: '4em 0' }}>
      <h1 style={{ textAlign: 'center' }}>為顯示完整網頁，建議使用Chrome或其他瀏覽器開啟，以獲得最佳瀏覽體驗。</h1>
    </div>
  ) : wrapWithProvider;


if (process.env.NODE_ENV === 'development') {
  const { getLCP, getFID, getCLS } = require('web-vitals');
  getCLS(console.log);
  getFID(console.log);
  getLCP(console.log);
}

(async () => {
  if (!window.ResizeObserver) {
    window.ResizeObserver = (await import('resize-observer-polyfill')).default;
  }
  if (!window.IntersectionObserver) {
    window.IntersectionObserver = (await import('intersection-observer-polyfill')).default;
  }
})()
